<template>
	<div class="v-knowledge-index" v-loading="loading">
		<!-- ÊêúÁ¥¢ -->
		<knowledgeSearch @onSearchKey="onSearchKey" />
		<!-- ÊêúÁ¥¢ÁªìÊûú -->
		<knowledgeList v-if="search" :list="list" :pagination="pagination" @onSearchKey="onSearchKey" />
		<template v-else>
			<!-- ‰æøÊç∑ÂÖ•Âè£ -->
			<!-- <wikiEntry /> -->
			<!--  Áé©Ê≥ïÊåáÂçó -->
			<wikiGuide :data="guide" />
			<!-- ÁÉ≠Èó®ÂâßÊÉÖ -->
			<wikiHot :data="hot" />
			<!-- ÁÉ≠Èó®ÂâßÊÉÖ -->
			<wikiRecent :list="recent" />
		</template>
	</div>
</template>

<script>
import knowledgeSearch from "@/components/knowledge/search.vue";
import knowledgeList from "@/components/knowledge/list.vue";
// import wikiEntry from "@/components/knowledge/entry.vue";
import wikiGuide from "@/components/knowledge/guide.vue";
import wikiHot from "@/components/knowledge/hot.vue";
import wikiRecent from "@/components/knowledge/recent.vue";
import { getKnowledgeSearch } from "@/service/knowledge.js";
import { getKnowledgeNewest, getMenuGroups } from "@/service/wiki.js";
import { getStatRank } from "@jx3box/jx3box-common/js/stat";
export default {
	name: "KnowledgeIndex",
	components: {
		knowledgeSearch,
		knowledgeList,
		// wikiEntry,
		wikiGuide,
		wikiHot,
		wikiRecent,
	},
	data: function () {
		return {
			loading: false,
			search: "",
			list: "",

			page: 1,
			per: 20,
			total: 1,
			pages: 1,

			guide: "",
			hot: {
				title: "ÁÉ≠Èó®ÂâßÊÉÖ",
				icon: "el-icon-collection",
				list: "",
			},
			recent: "",
		};
	},
	computed: {
		params() {
			let params = {
				limit: this.per,
				page: this.page,
				keyword: this.search,
			};
			return params;
		},
		pagination() {
			return {
				page: this.page,
				total: this.total,
				per: this.per,
			};
		},
	},
	methods: {
		// Ëé∑ÂèñÁé©Ê≥ïÊåáÂçó
		getGuideData() {
			getMenuGroups({ names: ["guide-pve", "guide-pvx", "guide-pvp"] }).then((res) => {
				this.guide = res.data;
			});
		},
		// Ëé∑ÂèñÁÉ≠Èó®
		getHotData() {
			getStatRank("plot", "views", 18).then((res) => {
				let list = [];
				res.data.forEach((item) => {
					if (item.name.startsWith("plot")) {
						let id = item.name.split("-").pop();
						list.push(id);
					}
				});
				getKnowledgeSearch({ ids: list.join() }).then((res) => {
					this.hot.list = res.data;
				});
			});
		},
		// Ëé∑ÂèñÊúÄÊñ∞Â∏ñÂ≠êÊï∞ÊçÆ
		getRecentData() {
			getKnowledgeNewest({ type: "knowledge" }).then((res) => {
				this.recent = res.newest;
			});
		},
		// Ëé∑ÂèñÊêúÁ¥¢ÁöÑÊï∞ÊçÆ
		getSearchData() {
			this.loading = true;
			getKnowledgeSearch(this.params)
				.then((res) => {
					this.list = res.data;
					this.total = res.total;
				})
				.finally(() => {
					this.loading = false;
				});
		},

		// ===============
		// ÊêúÁ¥¢ÂÖ≥ÈîÆËØçÊü•Êâæ
		onSearchKey(val) {
			this.search = val;
			this.page = 1;
		},
		// ÂàáÊç¢È°µÁ†Å
		onPageKey(val) {
			this.page = val;
		},
	},
	watch: {
		params() {
			this.getSearchData();
		},
	},
	created: function () {
		this.getGuideData();
		// this.getHotData();
		this.getRecentData();
	},
};
</script>
<style lang="less">
@import "~@/assets/css/knowledge/index.less";
</style>